<template>
	<view>
		<view class="flex flex-column" style="font-size: 50rpx;font-weight: 800;margin-left: 60rpx;">
			<text class="title" >小分店</text>
			<text class="title">轻松引爆门店客流</text>
		</view>
		
		<view class="border-bottom flex mt-10" style="margin-left: 60rpx;margin-right: 60rpx;">
			<text class="mr-2" style="font-size: 36rpx;font-weight: 500;">+86</text>
			<input class="mb-2" style="font-size: 36rpx;" type="number" value="" placeholder="请输入手机号" maxlength="11" v-model="phone"/>
		</view>
		
		<view class="border-bottom flex  mt-4" style="margin-left: 60rpx;margin-right: 60rpx;">
			<input  style="font-size: 36rpx;" type="number" value="" placeholder="输入验证码" v-model="code"/>
		    <view class="mb-2 border rounded-circle text-center pl-2 pr-2 pt-1 pb-1" style="width: 200rpx;"
			 @click="getCode()">
				<text>{{yzm}}</text>
			</view>
		</view>
		
		<view class="mt-8" style="margin-left: 60rpx;margin-right: 60rpx;" >
			<u-button :type="changetype" shape="circle" style="height: 100rpx;" @click="login()">
				<text class="font-md">登录</text>
			</u-button>
		</view>
		
	</view>
</template>

<script>
	export default {
		data() {
			return {
				phone:"",
				code:"",
				type:"disable",
				second:60,
				yzm:"获取验证码"
			}
		},
		
		computed:{
			changetype(){
				return this.getCompleted()
			}
		},
		
		methods: {
			
			//控制登录按钮变色
			getCompleted(){
				if(this.phone.length==11&&this.code!=""){
					return "success"
				}
				else{
					return "disable"
				}
			},
			
			//获取验证码
			getCode(){
				if(this.phone.length==11){
					var interval = setInterval(() => {
					        let times = --this.second
					        this.second = times<10?'0'+times:times ,//小于10秒补 0
							this.yzm=this.second+"秒"
					    }, 1000)
					    setTimeout(() => {
					        clearInterval(interval)
					        this.second=60
					        this.yzm = "重新获取验证码"
					    }, 60000)
				}
				else{
					uni.showToast({
						title:"请输入正确的手机号",
						icon:"none"
					})
				}
			},
			
			//登录
			login(){
				if(this.phone.length==11&&this.code!=""){
					// 调用登录方法
				}
			}
		}
	}
</script>

